@import 'vars';
@import 'button';
@import 'hover';
@import 'footer';
@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@200;300;700&display=swap');

* {
    font-family: Raleway;
}

.off_scroll {
    overflow-y: hidden;
}



.header {
    height: 85px;
    position: absolute;
    width: 100%;
    display: flex;


    &__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__burger {
        display: flex;
        flex-direction: column-reverse;
        position: relative;
        z-index: 100;

        @for $i from 1 through 3 {
            span:nth-child(#{$i}) {
                width: calc(13px * $i);
            }
        }

        span {
            height: 5px;
            background: white;
            margin-top: 5px;
            transition: all .5s ease;
        }
    }

    &__burger-list {
        display: none;
        font-size: 18px;
        font-weight: 300;
        text-transform: uppercase;
        color: white;
        width: 100%;

    }


    &__burger-wrapper {
        display: flex;
        gap: 25px;
    }



    &__burger-phone {
        display: block;
        width: 100%;
        height: 90%;
        background: white;
        position: fixed;
        top: 10%;
        left: 0;
        display: flex;
        align-items: center;
        flex-direction: column;
        list-style: none;
        transform: scaleY(0);
        transform-origin: left top;
        transition: all .5s ease;

        li {
            width: 100%;
        }

        a {
            font-size: 16px;
            font-weight: 400;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }

    &__burger-menu {
        margin-top: 20px;
        width: 90%;
        list-style: none;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;

        li {
            width: 100%;
            display: flex;
            border-top: 0.3px solid #c2c1c1;
            align-items: center;

            &:last-child {
                border-bottom: 0.3px solid #c2c1c1;
            }

            img {
                filter: brightness(.5);
            }
        }

        a {
            font-size: 16px;
            font-weight: 400;
            width: 100%;
            height: 60px;
            display: flex;
            justify-content: center;
            align-items: center;
            text-transform: uppercase;
        }
    }

    &__burger-footer {
        margin-top: 75px;
        display: flex;
        gap: 20px;

        i {
            color: $accent;
            height: 30px;
            font-size: 24px;
        }
    }

    &__burger-wrapper {
        margin-top: 50px;
    }

    &__burger-search {
        width: 100%;
        display: flex;
        align-items: center;

        position: relative;

        input {
            width: 200px;
            height: 40px;
            border: 1px solid #b1b1b1;
            border-radius: 20px;
            padding-left: 15px;
            font-size: 14px;

            &:focus {
                outline: none;
            }
        }

        img {
            position: absolute;
            filter: brightness(0);
            width: 15px;
            right: 15px;
        }
    }

    &__logo {
        transition: all .3s ease;
    }

    &__nav {
        display: flex;
        gap: 25px;
        transition: all .3s ease;

        a {
            font-size: 18px;
            font-weight: 300;
            color: white;
            text-transform: uppercase;

            &:hover {
                color: rgb(211, 210, 210);
                transition: all .5s;
            }
        }
    }

    &__select {
        display: flex;
        gap: 25px;
        transition: all .3s ease;

        select {
            background: none;
            color: white;

            option {
                background: transparent;
            }
        }


    }

    &__actions {
        display: flex;
        gap: 30px;
        transition: all .5s ease;
    }

    &__new {}

    &__new-title {}

    &__new-divider {}

    &__new- {}
}

.new {
    height: 100vh;
    background: url('../img/header/bg.jpg') center / cover;
    padding-top: 45vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;

    &__title {
        font-size: 46px;
        font-weight: 300;
        color: white;
    }

    &__divider {
        width: 120px;
        height: 1px;
        background: white;
    }

    &__catalog {
        font-size: 16px;
        font-weight: 200;
        color: white;
        text-transform: uppercase;
        display: flex;
        align-items: center;

        span {
            margin-left: 10px;
        }
    }
}

.active-burger {
    transition: all .5s ease;

    span {
        &:first-child {
            width: 40px;
            transform: rotate(45deg) translate(-14px);
        }

        &:nth-child(2) {
            transform: scaleY(0);
        }

        &:last-child {
            width: 40px;
            transform: rotate(-45deg) translate(-14px);
        }
    }
}

.active-list {
    display: flex;
    transform: scaleX(1);
}

.active-mobile {
    transition: all .5s ease;
    transform: scaleY(1);
}

.hidden {
    display: none;
}

.opacity {
    opacity: 0;
}

.scale {
    animation: scale 1s forwards;
}

.scale-back {
    animation: scale_back 1s backwards;
}


@keyframes scale {
    from {
        transform: scaleY(0);
        transform-origin: top;
    }

    to {
        transform: scaleY(1);
        transform-origin: top;
    }
}


@keyframes scale_back {
    from {
        transform: scaleY(0);
        transform-origin: bottom;
    }

    to {
        transform: scaleY(1);
        transform-origin: bottom;
    }
}

.categories {

    &__title {
        font-size: 36px;
        font-weight: 300;
        margin-top: 100px;
    }

    &__cards {
        margin-top: 50px;
    }

    &__card {}
}

.card {
    position: relative;

    &__text {
        position: absolute;
        bottom: 0;
        text-align: center;
        width: 100%;
        left: 0;
        height: 30px;
        background: rgba(224, 190, 162, 0.5);
        backdrop-filter: blur(3px);
        font-size: 20px;
        font-weight: 300;
        color: white;
    }
}

.sub {
    margin-top: 100px;

    &__wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__title {
        font-size: 36px;
        font-weight: 300;
        text-align: center;
    }

    .button {
        width: 600px;
        height: 50px;
        background: $accent;
        color: white;
        font-size: 20px;
        font-weight: 200;
        margin-top: 20px;
        box-shadow: 0px 2px 5px transparent;
        transition: all .3s ease;

        &:hover {
            box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
            transition: all .3s ease;
        }

        &:active {
            background: #CCA88A;
            box-shadow: inset 0px 2px 5px rgba(0, 0, 0, 0.3);
        }
    }

    &__email {
        margin-top: 50px;
        width: 600px;
        height: 50px;
        text-align: center;
        border: 0.5px solid #252525;
        transition: all .3s ease;

        &:focus {
            outline: none;
            border: 0.5px solid $accent;
            color: #252525;
            transition: all .3s ease;
        }
    }

    &__text {
        width: 600px;
        text-align: center;
        margin-top: 20px;
        font-size: 16px;
        font-weight: 200;
    }
}


.categories {
    overflow: hidden;

    &__slider {}

    &__cards {
        width: 1200px;
        overflow-x: hidden;
        display: flex;
        position: relative;
        transition: all .5s ease;
    }

    &__card {
        width: 275px;
        min-width: 275px;
        transition: all .5s ease;

    }
}

.prev,
.next {
    width: 40px;
    height: 40px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 20;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #E0BEA2;
    color: white;
    font-size: 26px;
}


.prev {
    left: 25px;
}

.next {
    right: 25px;
}

.active-slide {
    margin: 0 !important;
}

.card {
    &__text {
        width: 100%;
    }
}

@import '../adaptive/index-adp';